import CLASS_LIST from './class-list';

const sliderLine = document.querySelector('.favorite__slider-line');
const paginationLines = document.querySelectorAll('.slider-pagination-line');
const nextBtn = document.querySelector('.slider-navigation-next');
const prevBtn = document.querySelector('.slider-navigation-prev');
const slides = document.querySelectorAll('.slider-slide');

const sliderLineStyle = getComputedStyle(sliderLine);

let slideWidth = slides[0].clientWidth;
let slideOffset = parseInt(sliderLineStyle.getPropertyValue('gap'), 10);
let slideSize = slideWidth + slideOffset;

let displacement = 0;
let currentSlide = 0;
let startX = 0;
let endX = 0;
let intervalId;
let lastWindowWidth;

const changeActivePagination = (activeSlide) => {
  paginationLines.forEach((line) => {
    line.classList.remove(CLASS_LIST.favoriteSliderPaginationLineActive);
  });
  paginationLines[activeSlide].classList.add(CLASS_LIST.favoriteSliderPaginationLineActive);
};

const nextSlide = () => {
  currentSlide -= 1;
  if (displacement === 0) {
    currentSlide = paginationLines.length - 1;
    displacement -= (paginationLines.length - 1) * slideSize;
  } else displacement += slideSize;

  paginationLines.forEach((line, index) => {
    if (line.classList.contains(CLASS_LIST.favoriteSliderPaginationLineActive)) paginationLines[index].style.transformOrigin = 'left';
    else paginationLines[index].style.transformOrigin = 'right';
  });
  sliderLine.style.transform = `translateX(${displacement}px)`;
  nextBtn.disabled = true;

  setTimeout(() => {
    nextBtn.disabled = false;
  }, 1000);

  changeActivePagination(currentSlide);
};

const prevSlide = () => {
  currentSlide += 1;
  if (displacement === -(paginationLines.length - 1) * slideSize) {
    currentSlide = 0;
    displacement = 0;
  } else displacement -= slideSize;

  paginationLines.forEach((line, index) => {
    if (line.classList.contains(CLASS_LIST.favoriteSliderPaginationLineActive)) paginationLines[index].style.transformOrigin = 'right';
    else paginationLines[index].style.transformOrigin = 'left';
  });
  sliderLine.style.transform = `translateX(${displacement}px)`;
  prevBtn.disabled = true;

  setTimeout(() => {
    prevBtn.disabled = false;
  }, 1000);

  changeActivePagination(currentSlide);
};

window.addEventListener('resize', () => {
  if (window.innerWidth !== lastWindowWidth) {
    displacement = 0;
    currentSlide = 0;
    slideWidth = slides[0].clientWidth;
    slideOffset = parseInt(sliderLineStyle.getPropertyValue('gap'), 10);
    slideSize = slideWidth + slideOffset;
    sliderLine.style.transform = 'translateX(0px)';
    changeActivePagination(currentSlide);
    lastWindowWidth = window.innerWidth;
  }
});

const pauseInterval = () => {
  clearInterval(intervalId);
};

const startInterval = () => {
  pauseInterval();
  intervalId = setInterval(() => {
    prevSlide();
  }, 7000);
};

nextBtn.addEventListener('click', () => {
  pauseInterval();
  startInterval();
  nextSlide();
});

prevBtn.addEventListener('click', () => {
  pauseInterval();
  startInterval();
  prevSlide();
});

const handleMouseOut = (slide) => {
  startX = slide.offsetX;
};

const handleMouseDown = (slide) => {
  endX = slide.offsetX;
  if (startX < endX && endX - startX > slideWidth / 4) nextSlide();
  else if (startX > endX && startX - endX > slideWidth / 4) prevSlide();
  startInterval();
};

const handleTouchStart = (event) => {
  pauseInterval();
  startX = event.touches[0].clientX;
};

const handleTouchEnd = (event) => {
  endX = event.changedTouches[0].clientX;
  if (endX - startX > slideWidth / 2) nextSlide();
  else if (startX - endX > slideWidth / 2) prevSlide();

  pauseInterval();
  startInterval();
};

slides.forEach((slide) => {
  slide.addEventListener('mouseover', pauseInterval);
  slide.addEventListener('mouseout', startInterval);
  slide.addEventListener('mousedown', (el) => handleMouseOut(el));
  slide.addEventListener('mouseup', (el) => handleMouseDown(el));
  slide.addEventListener('touchstart', handleTouchStart);
  slide.addEventListener('touchend', handleTouchEnd);
});

startInterval();
